@import url(https://fonts.googleapis.com/css2?family=Noto+Serif:wght@400;700&display=swap);
:root{
  --primary: #efb6b2;
  --secondary: #4e4e4e;
  --error: #ff4a4a;
}

/* base styles & title */
body{
  font-family: "Noto Serif";
  color: #4e4e4e;
  color: var(--secondary);
}
.App{
  max-width: 960px;
  margin: 0 auto;
}
.title h1{
  color: #efb6b2;
  color: var(--primary);
  font-size: 1.2rem;
  letter-spacing: 2px;
  font-weight: normal;
}
.title h2, .title p{
  text-align: center;
}
.title h2{
  margin-top: 60px;
  font-size: 2.6rem;
}

/* upload form styles */
.output {
  height: 60px;
  font-size: 0.8rem;
}
.error {
  color: #ff4a4a;
  color: var(--error);
}
label {
  height: 30px;
  width: 30px;
  color: #efb6b2;
  color: var(--primary);
  border: 1px solid #efb6b2;
  border: 1px solid var(--primary);
  border-radius: 50%;
  display: inline-block;
  cursor: pointer;
  font-size: 24px;
  font-weight: bold;
  margin: 10px auto;
  line-height: 30px;
}
label:hover {
  background-color: #efb6b2;
  background-color: var(--primary);
  color: white;
}


/* progress bar styles */
.progress-bar {
  margin-top: 20px;
  height: 5px;
  background: #efb6b2;
  background: var(--primary);
}


/* image grid styles */
.img-grid {
  margin: 20px auto;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 40px;
}
.img-wrap {
  overflow: hidden;
  height: 0;
  /* Padding controls height */
  padding: 50% 0;
  position: relative;
  opacity: 0.8;
}
.img-wrap img {
  min-width: 100%;
  max-width: 150%;
  min-height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}
/* modal styles */
.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
}
.backdrop img {
  display: block;
  max-width: 60%;
  max-height: 80%;
  margin: 60px auto;
  border: 3px solid white;
  box-shadow: 3px 5px 7px rgba(0, 0, 0, 0.5);
}
